import egor from "../assets/image/dialogsAvatar/egor.jpg";
import angela from "../assets/image/dialogsAvatar/angela.jpg";
import roman from "../assets/image/dialogsAvatar/roman.jpg";
import olga from "../assets/image/dialogsAvatar/olga.jpg";
import nata from "../assets/image/dialogsAvatar/nata.jpg";

const ADD_MESSAGE = "dialogs/ADD_MESSAGE";

let initialState = {
    dialogs: [
        {id: 1, name: "", avatar: egor},
        {id: 2, name: "", avatar: angela},
        {id: 3, name: "", avatar: roman},
        {id: 4, name: "", avatar: olga},
        {id: 5, name: "", avatar: nata},
    ],
    messages: [
        {id: 1, message: "Hi"},
        {id: 2, message: "How are you?"},
        {id: 3, message: "Yes"},
        {id: 4, message: "Yes"},
        {id: 5, message: "Find"},
    ],
};

const dialogsPageReducer = (state = initialState, action) => {
    switch (action.type) {
        case ADD_MESSAGE: {
            let newMessage = {
                id: state.messages.length + 1,
                message: action.newMessage
            }
            return {
                ...state,
                messages: [...state.messages, newMessage],
            };
        }
        default: {
            return state;
        }
    }
}

export const addMessage = (newMessage) => ({type: ADD_MESSAGE, newMessage})

export default dialogsPageReducer;